import React from 'react'
import styled from 'styled-components'
import SlickCarousel from 'react-slick'

import 'slick-carousel/slick/slick.css'
import 'slick-carousel/slick/slick-theme.css'

const LEFT = 'left'
const RIGHT = 'right'

const StyledCarousel = styled.div`
  .slick-next {
    ::before {
      display: none;
    }
  }
  .slick-prev {
    ::before {
      display: none;
    }
  }
  .slick-disabled {
    display: var(--display);
    .carousel__arrow {
      stroke: ${props => props.theme.colors.greyLight} !important;
    }
  }
`

const ArrowWrapper = ({
  children,
  currentSlide,
  slideCount,
  arrowStyle,
  onArrowClick,
  direction,
  ...props
}) => {
  const handleArrowClick = () => {
    if (props.onClick) props.onClick() // react-slick onClick to handle changing slides
    onArrowClick() // allow additional events to fire
  }

  return (
    <button
      {...props}
      onClick={handleArrowClick}
      style={arrowStyle}
      type='button'
      aria-label={direction === LEFT ? 'Previous slide' : 'Next slide'}
    >
      {React.cloneElement(children, {
        direction,
        style: arrowStyle,
      })}
    </button>
  )
}

const Arrow = ({ direction, style }) => {
  const defaultStyle = {
    transform: direction === LEFT ? 'scaleX(-1)' : null,
    width: '14px',
    height: '24px',
    stroke: 'black',
  }

  return (
    <svg
      style={{ ...defaultStyle, ...style }}
      viewBox='0 0 19 34'
      version='1.1'
      className='carousel__arrow'
    >
      <g
        strokeWidth='1.5'
        strokeLinecap='round'
        strokeLinejoin='round'
        fill='none'
        transform='translate(-942.000000, -343.000000)'
      >
        <polyline points='943 343 960 360 943 377' />
      </g>
    </svg>
  )
}

const StyledList = styled.ul`
  li {
    height: 6px;
    width: 6px;
  }
`

const defaultDots = dots => <StyledList>{dots}</StyledList>

const Carousel = ({
  children,
  className,
  slidesToShow,
  slidesToScroll,
  dotsStyle,
  dotClass,
  showDots,
  showArrows,
  showDisabledArrows,
  leftArrowStyle,
  rightArrowStyle,
  centerSlide,
  infinite,
  prevArrow,
  nextArrow,
  onArrowClick,
  customDots,
  lazyLoad,
  ...props
}) => {
  const settings = {
    ...props,
    arrows: showArrows,
    dots: showDots,
    infinite,
    slidesToShow,
    slidesToScroll,
    centerMode: centerSlide,
    className: 'quince-carousel',
    prevArrow: (
      <ArrowWrapper
        onArrowClick={() => onArrowClick(LEFT)}
        direction={LEFT}
        arrowStyle={leftArrowStyle}
      >
        {prevArrow}
      </ArrowWrapper>
    ),
    nextArrow: (
      <ArrowWrapper
        onArrowClick={() => onArrowClick(RIGHT)}
        direction={RIGHT}
        arrowStyle={rightArrowStyle}
      >
        {nextArrow}
      </ArrowWrapper>
    ),
    appendDots: customDots || defaultDots,
    lazyLoad,
  }

  return (
    <StyledCarousel
      className={className}
      style={{ '--display': showDisabledArrows ? 'unset' : 'none' }}
    >
      <SlickCarousel {...settings}>{children}</SlickCarousel>
    </StyledCarousel>
  )
}

Carousel.defaultProps = {
  children: null,
  slidesToShow: 1,
  slidesToScroll: 1,
  dotsStyle: {},
  dotClass: 'carousel-dot carousel-dot--dark',
  showDots: false,
  showArrows: true,
  showDisabledArrows: true,
  leftArrowStyle: {},
  rightArrowStyle: {},
  centerSlide: false,
  infinite: false,
  prevArrow: <Arrow />,
  nextArrow: <Arrow />,
  onArrowClick: () => {},
  customDots: null,
  lazyLoad: 'progressive',
  autoplay: false,
  autoplaySpeed: 2000,
}

export default Carousel
